body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  color: white; }

ul {
  margin-bottom: 5px; }

li {
  margin-left: 30px; }

body {
  font-family: 'Questrial', sans-serif;
  background: url(/Pokedex-app/static/media/back.c0cb83aa.jpg);
  background-size: 100%; }

.header {
  width: 60%;
  height: auto;
  margin: 100px auto 15px auto;
  padding: 20px 0;
  text-align: center;
  background-color: #C10000; }
  .header h5 {
    padding-left: 40px; }

.filter {
  width: 60%;
  height: auto;
  margin: 0 auto 15px auto;
  padding: 13px 0;
  background-color: #C10000; }
  .filter input {
    background-color: transparent;
    border: none;
    border-bottom: solid 2px white;
    width: 25%;
    margin: 0 20px 0 15px;
    font-size: 20px;
    outline: none; }
    .filter input::-webkit-input-placeholder {
      color: white; }
    .filter input::-ms-input-placeholder {
      color: white; }
    .filter input::placeholder {
      color: white; }
  .filter select {
    background-color: transparent;
    border: none;
    margin-left: 5px;
    outline: none; }
    .filter select:hover {
      cursor: pointer; }
    .filter select option {
      background: #C10000; }

.menu {
  display: -webkit-flex;
  display: flex;
  width: 60%;
  height: 400px;
  margin: 0 auto; }
  .menu .list {
    background: #C10000;
    width: 30%;
    margin-right: 15px; }
    .menu .list .pokemons {
      background-color: black;
      margin: 75px auto 0 auto;
      width: 70%;
      text-align: center;
      height: 200px;
      border-radius: 10%;
      overflow: scroll;
      overflow: -moz-scrollbars-none;
      -ms-overflow-style: none; }
      .menu .list .pokemons::-webkit-scrollbar {
        width: 0; }
      .menu .list .pokemons p:hover {
        color: orange;
        cursor: pointer; }
    .menu .list .buttons {
      width: 100%;
      text-align: center; }
      .menu .list .buttons button {
        margin: 10px 10px 0 0;
        padding: 5px;
        background: black;
        border: none;
        border-radius: 20%;
        outline: none; }
        .menu .list .buttons button:hover {
          cursor: pointer;
          color: orange; }
  .menu .disp {
    background: #C10000;
    width: 70%;
    padding-top: 10px; }
    .menu .disp p {
      margin-bottom: 5px; }
    .menu .disp .infobox {
      background: black;
      padding: 20px 10%;
      width: 50%;
      height: 60%;
      border-radius: 5%;
      margin: 7% auto 0 auto; }
      .menu .disp .infobox .placehold {
        text-align: center; }

@media screen and (max-width: 876px) {
  .menu {
    display: block;
    height: auto; }
  .menu .list {
    width: 100%;
    margin-bottom: 15px;
    padding: 10px 0 50px 0; }
  .menu .disp {
    width: 100%;
    margin-bottom: 75px;
    padding: 40px 0; } }

@media screen and (max-width: 385px) {
  .menu .disp .infobox {
    width: 70%; }
  .disp {
    padding: 40px 0; } }

